.auth-page-wrapper {    
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: calc(100vh - 84px);

    .left-column {
        background-size: cover;
    }

    .right-column {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $offwhite;
        color: $blue;

        .auth-form-wrapper {
            @include input-element();
            @include base-grid();
            grid-gap: 21px;
            padding: 42px;

            .from-group {
                display: flex;

                svg {
                    font-size: 2em;
                    color: $teal;
                    margin-right: 15px;
                }
                
            }
        }
    }
}